
	import {
		mapMutations
	} from 'vuex';
	export default {
		methods: {
			...mapMutations(['login','logout'])
		},
		onLaunch: function() {
			let unifiedUser = uni.getStorageSync('unifiedUser') || '';
			let token = uni.getStorageSync('shuogesha_tk') || '';
			if(unifiedUser.id&&token){
				//更新登陆状态
				uni.getStorage({
					key: 'unifiedUser',
					success: (res) => {
 						this.login({unifiedUser:res.data});
					}
				});
			}else{
				this.logout();
			}
			
		},
		onShow: function() {
			console.log('App Show')
		},
		onHide: function() {
			console.log('App Hide')
		}
	}
